<template>
  <div class="parts">
    <div class="title"><h3>家电</h3></div>
    <div class="centext">
      <div class="centext_left">
        <ul>
          <li><slot name="img1"></slot></li>
          <li><slot name="img2"></slot></li>
        </ul>
      </div>
      <div class="centext_right">
        <ul>
          <li v-for="(item,index) in parts" v-if="index<7">
            <div><img :src="item.originalImg" alt=""></div>
            <div class="pad_lr">{{item.goodsName}}</div>
            <div class="pad_lr">70英寸震撼巨屏，4K画质，细腻如真</div>
            <div class="cored"><strong>3249元</strong><del>3799元</del></div>
          </li>

          <li>
            <div class="box_top">
              <div>
                <h4>小米壁画电视65英寸</h4>
                <strong>6999元</strong>
              </div>
              <div class="box_okk">
                <img src="https://cdn.cnbj1.fds.api.mi-img.com/mi-mall/74e573c4c0d89048392d14831cc507d5.jpg?thumb=1&w=125&h=125&f=webp&q=90" alt="">
              </div>
            </div>
            <div class="box_bottom">
              <div class="box_bottom_text">
                <h2>浏览更多</h2>
                <strong>电视影音</strong>
              </div>
              <div class="box_okk jiantou" >
                <img src="~@/assets/icon/right_this.png" alt="">
              </div>
            </div>


          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
  export default{
    data(){
      return{
        parts:[]
      }
    },
    mounted() {
      this.axios.get(this.ojbk+"/result/front/goods_classify").then((res)=>{

        this.parts=res.data.家电.indexGoodsBeans


      })
    }

  }
</script>

<style>
  .title{
    height: 50px;
    width: 1300px;
    margin: 0 auto;
    line-height: 50px;
  }
  .parts{
    width: 100%;
    background-color: #F5F5F5;

  }
  .pad_lr{
    padding: 0 20px;
    box-sizing: border-box;
    color: #A1A1A1;
  }
  .centext{
    width: 1300px;
    display: flex;
    margin: 0 auto;
  }
  .centext_left{
    flex: 2;
  }
  .centext_left ul li{
    width: 240px;
    height: 300px;
    list-style: none;
    margin-bottom: 10px;
  }
  .centext_left ul li img{
    width: 100%;
    height: 100%;
  }
  .centext_right{
    flex: 8;
    display: flex;
  }
  .centext_right ul li{
    list-style: none;
    width: 250px;
    font-size: 10px;
    background-color: #FFFFFF;
    margin-left: 10px;
    margin-bottom: 10px;
    text-align: center;
    padding-top: 20px;
    box-sizing: border-box;
    height: 300px;
    transition: 0.3s;
  }
  .centext_right ul li:hover{
    box-shadow:  2px 2px 10px #e1e1e1;
  }
  .centext_right ul li img{
    width: 160px;
    height: 160px;
  }
  .centext_right ul{
    display: flex;
    flex-wrap: wrap;
    background-color: #F5F5F5;
  }
  .box_top{
    background-color: #ffffff;
    display: flex;
    align-items: center;
    padding: 0 20px;
    margin-top: 20px;
  }
  .box_okk img{
    width: 120px!important;
    height: 120px!important;
  }
  .box_bottom{
    display: flex;
    align-items: center;
  }
  .box_bottom_text{
    padding-left: 40px;
  }
  .cored{
    color: red;
  }



</style>



