<script>
  export default{
    data(){
      return{
        arr:['小米秒杀','企业团购','F码通道','米粉卡','以旧换新','话费充值']
      }
    }
  }

</script>

<style>
  .navbarbottom{
    width: 100%;
    height: 200px;
    background-color: #F5F5F5;
    padding-bottom: 20px;
  }
  .navbar_box{
    width: 1300px;
    margin: 0 auto;
    display: flex;
    background-color: #F5F5F5;
    padding-top: 20px;
  }
  .navbar_box div{
    flex: 1;
    height: 200px;
    background: #5f5750;
    margin-left: 10px;
    color: #A1A1A1;
  }
  .navbar_box div img{
