  .first_box{
    flex: 1;
    margin-left: 0!important;
    background-color: #FFA500;
  }
  .first_box ul li{
    list-style: none;
    width: 100px;
    height: 100px;
    line-height: 100px;
    text-align: center;
    align-items: center;
  }
  .first_box ul{
    display: flex;
    flex-wrap: wrap;
  }

</style>



