<template>

  <el-container>
    <el-header>小米后台管理系统</el-header>
    <el-container class="h">
      <el-aside width="200px">

        <el-menu default-active="2" class="el-menu-vertical-demo" @open="handleOpen" @close="handleClose">
            <el-submenu index="1">
                     <template slot="title"><i class="el-icon-message"></i>导航一</template>
                     <router-link to="/admin/a"><el-menu-item index="1-1">首页横幅</el-menu-item></router-link>
                     <router-link to="/admin/b"><el-menu-item index="1-2">导航栏</el-menu-item></router-link>
                     <el-menu-item index="1-3">取首页目录数据</el-menu-item>
                     <el-menu-item index="1-3">取首页横幅数据</el-menu-item>
                   </el-submenu>
                   <el-submenu index="2">
                     <template slot="title"><i class="el-icon-menu"></i>导航二</template>
                   </el-submenu>
                   <el-submenu index="3">
                     <template slot="title"><i class="el-icon-setting"></i>导航三</template>
                   </el-submenu>
                 </el-menu>
          </el-menu>



      </el-aside>
      <el-main><router-view></router-view></el-main>
    </el-container>
  </el-container>

</template>

<script>
  export default {
    methods: {
      handleOpen(key, keyPath) {
        console.log(key, keyPath);
      },
      handleClose(key, keyPath) {
        console.log(key, keyPath);
      }
    }
  }
</script>


<style>
  .el-container {
    height: 100%;
  }

  .el-header,
  .el-footer {
    background-color: #B3C0D1;
    color: #333;
    text-align: center;
    line-height: 160px;
    min-height: 160px;
  }

  .el-aside {
    background-color: #D3DCE6;
    color: #333;
    text-align: center;
    line-height: 200px;
  }


  .el-main {
    background-color: #E9EEF3;
    color: #333;
    text-align: center;
    line-height: 160px;
  }

  body>.el-container {
    margin-bottom: 40px;
  }

  .el-container:nth-child(5) .el-aside,
  .el-container:nth-child(6) .el-aside {
    line-height: 260px;
  }

  .el-container:nth-child(7) .el-aside {
    line-height: 320px;

  }
</style>



