<template>
    <div class="register">
    <el-row>
      <el-col :span="24">
        <div class="grid-content bg-purple-dark">
          <div class="box">
            <div class="register_logo"><img src="~@/assets/icon/homelogin.png" alt=""></div>
            <div class="register_title"><h2>注册小米账号</h2></div>
            <div class="register_text">
              <!-- <div>注册的手机号</div> -->
              <div>
                <el-tooltip class="item" effect="dark" content="请输入11位数手机号" placement="right">
                      <el-input v-model="phone" placeholder="注册的手机号"></el-input>
                </el-tooltip>



              </div>
             </div>
            <div class="register_text">
              <!-- <div>输入注册密码</div> -->
              <div>
                <el-tooltip class="item" effect="dark" content="请输入至少6位数的密码" placement="right">
                  <el-input v-model="pas" placeholder="输入注册密码" type="password"></el-input>
                </el-tooltip>
              </div>
             </div>
             <div class="register_text">
               <!-- <div>确认注册密码</div> -->
               <div>
                 <el-tooltip class="item" effect="dark" content="请确认与输入的密码是否一致" placement="right">
                 <el-input v-model="checkpas" placeholder="确认注册密码" type="password"></el-input>
                 </el-tooltip>
                 </div>
             </div>
             <div class="register_text">

               <div class="code">
                <!-- <el-tooltip class="item" effect="dark" content="" placement="right"> -->
                 <el-input v-model="code" placeholder="验证码" type="text"></el-input>
                 </el-tooltip></div>
                  <span @click="getcodeimgurl"><img :src="codeimgurl" alt=""></span>

             </div>
             <div class="register_btn">
               <el-button type="primary" plain @click="check">提交注册</el-button>
               <el-button type="warning" plain @click="tologin">马上登录</el-button>
             </div>
             <div class="ageencheck">
               <el-checkbox v-model="checked">已阅读并同意：小米 用户协议和 隐私政策</el-checkbox>
             </div>
          </div>

        </div>
        </el-col>
    </el-row>
</div>
</template>

<script>
  export default{
    data(){
      return{
        input:'',
        checked: true,
        phone:'',
        checkpas:'',
        pas:'',
        codeimgurl:'',
        code:''
      }
    },
    methods:{
      postrst(){
        this.axios.post(this.ojbk+'/user/register',{
          "userName":this.phone,
          "passWord":this.pas,
          "againpassWord":this.pas,
          "captchaImgValue":this.code,
          "captchaImgUrl":this.codeimgurl
        }).then((res)=>{
          // console.log(res)
          if(res.data.code=="00000"){
            this.$message("注册成功，快来剁手吧，为您跳转到首页哦")

            this.$router.push("/home")
          }else{
            this.$message('注册失败'+res.data.message)
          }

        }).catch((error)=>{
          console.log(error)
        })
      },
      check(){
        console.log(this.os)
        const reg=/0?(13|14|15|17|18|19)[0-9]{9}/;
        const pasreg=/^[a-z0-9_-]{6,18}/
        if(!reg.test(this.phone)){
          return this.$message('手机号错误')
        }
        if(this.pas===''||this.checkpas===''){
          return this.$message('密码不能为空')
        }
        if(!pasreg.test(this.pas)){
          return this.$message('密码至少6位数')
        }
        if(this.pas!==this.checkpas){
          return this.$message('密码不相同，请重新输入')
        }
        if(this.checked!==true){
          return this.$message('请阅读小米协议和用户协议')
        }
        this.postrst();
      },
      getcodeimgurl(){
        this.axios.get(this.ojbk+'/auth/create_kaptcha',).then((res)=>{
           this.codeimgurl=res.data.kaptchaImgUrl
           console.log(res.data)
         })
      },
      tologin(){
        this.$router.push('/login')
      }
    },
    mounted() {

       this.axios.get(this.ojbk+'/auth/create_kaptcha',).then((res)=>{
       this.codeimgurl=res.data.kaptchaImgUrl
       console.log(res.data)
        })

    },
     computed:{
       os(){
         return this.$store.state.name
       }
     }

}
</script>

<style>
  body,html{
    background-color: #f9f9f9;
  }
  .register{
    padding:20px 0px ;
    box-sizing: border-box;
  }
  .box{
    margin: 0 auto;
    width: 850px;
    height: 640px;
    background-color: #FFFFFF;
  }
  .register_logo{
    text-align: center;
    padding-top: 20px;
    padding-bottom: 40px;
  }
  .register_title{
    text-align: center;
  }
  .register_text{
    width: 400px;
    margin: 0 auto;
    padding: 10px 10px;
    font-size: 12px;
  }
  .register_btn{
    width: 400px;
    margin: 0 auto;
    text-align: center;
  }
  .ageencheck{
    text-align: center;
    padding-top: 30px;
    box-sizing: border-box;
  }
  .register_text img{
    width: 130px;
    height: 40px;
  }
  .code{
    width: 200px;
    height: 45px;
    float: left;
    margin-right: 30px;
  }
  .code input{
    width: 180px;
    height: 40px;
    margin-right: 20px;
  }
  .code img{
    height: 40px;
  }
</style>



