import Vue from 'vue'
import Router from 'vue-router'
import home from '@/view/home/home.vue'
import login from '@/view/login/login.vue'
import register from '@/view/register/register.vue'
import adminim from '@/view/admin/adminimp.vue'
import acc from '@/view/admin/aditem/a.vue'
import bcc from '@/view/admin/aditem/b.vue'
import details from '@/view/details/details.vue'
Vue.use(Router)

export default new Router({
  mode:'history',
  routes: [
    {
      path: '/',
      component:home,
      meta:{
        showTab:true,
        title:"小米商城|小米之家"
      }
    },
    {
      path:'/home',
      component:home,
      meta:{
        showTab:true,
        title:"小米商城|小米之家"
      }
    },
    {
      path:'/login',
      component:login,
      meta:{
        showTab:false,
        title:"登录_小米商城"
      }
    },
    {
      path:'/register',
      component:register,
      meta:{
       showTab:false,
       title:"注册_小米商城"
      }
    },
    {
      path:'/details/:id',
      component:details,
      meta:{
       showTab:true,
       title:"产品详情页"
      }
    },
    {
      path:'/admin',
      component:adminim,
       children: [
              {
                // 当 /user/:id/profile 匹配成功，
                // UserProfile 会被渲染在 User 的 <router-view> 中
                path: 'a',
                component: acc
              },
              {
                // 当 /user/:id/posts 匹配成功
                // UserPosts 会被渲染在 User 的 <router-view> 中
                path: 'b',
                component: bcc
              }
            ]
    },
    // {
    //   path:'/admin',
    //   component:a
    // }
  ]
})



