<template>
  <div class="nav">
    <div class="nav_center">
      <div class="nav_center_left">
        <ul>
          <li>小米商城</li>
          <li>MIUI</li>
          <li>ioT</li>
          <li>云服务</li>
          <li>金融</li>
          <li>有品</li>
          <li>小爱开放平台</li>
          <li>企业团购</li>
          <li>资质证照</li>
          <li>协议规则</li>
          <li>下载app</li>
          <li>智能生活</li>
          <li>select Location</li>
        </ul>

      </div>
      <div class="nav_center_right">
        <ul>
          <li v-show="showlogin" class="username">{{showlogin}}
            <div class="showlogin_panel">
              <ul>
                <li>个人中心</li>
                <li>评价晒单</li>
                <li>我的喜欢</li>
                <li>小米账户</li>
                <li @click="logout">退出登录</li>
              </ul>
            </div>
          </li>
          <li v-show="!showlogin"><router-link to="/login">登录</router-link></li>
          <li v-show="!showlogin"><router-link to="/register">注册</router-link></li>
          <li>消息通知</li>
          <li>购物车(0)</li>
        </ul>
      </div>

  </div>
  </div>
</template>

<script>
  export default{
    data(){
      return{
        flag:'',
        username:''
      }
    },
    mounted() {
        console.log(this.showlogin)
        // this.showlogin()
    },
    methods:{
      // 退出登录
      logout(){
        localStorage.removeItem("username")
        this.$router.push("/login")
      },

    },
    computed:{
      showlogin(){
        return this.$store.state.name
      }
    },


  }
</script>

<style>
  .nav{
    width: 100%;
    height: 40px;
    background-color: #484848;

  }
  .nav_center{
    margin: 0 auto;
    width: 1300px;
    display: flex;
    background-color: #484848;
  }
  .nav_center_left{
   flex: 8;
  }
  .nav_center_right{
    flex: 2;
  }
  .nav_center ul{

  }
  .nav_center ul li{
    float: left;
    height: 40px;
    padding: 0 5px;
    text-align: center;
    line-height: 40px;
    color: #a1a1a1;
    list-style: none;
    font-size: 12px;
    position: relative;
  }
  .nav_center ul li a{
   text-decoration: none;
   color: #a1a1a1;
  }
  .showlogin_panel{
    position: absolute;
    width: 80px;

    background-color: #FFFFFF;
    display: none;
    z-index: 10;
  }
  .showlogin_panel ul{
    display: flex;
    flex-direction: column;
  }
  .showlogin_panel ul li:hover{
      color: red;
      cursor: pointer;
   }
  .username:hover .showlogin_panel{
      display: block;
  }


</style>



