<template>
  <div class="shangou">
    <div class="shangou_box">
      <div class="slide_left">
        <ul class="slide_left_list">
          <li v-for="(item,index) in goods" @mousemove="move(index)" :key="index">{{item.name}}
          <div class="goods_box_list">
             <ul>
                <li v-for="it in item.indexGoodsBeans" class="arrlist">
                  <div>
                    <img :src="it.originalImg" alt="">
                    <p>{{it.goodsName}}</p>
                  </div>

                  <!-- <div><img :src="item.goodsDirBean.originalImg" alt=""></div>
                  <div><p>{{item.goodsDirBean.goodsName}}</p></div> -->
                </li>
              </ul>
          </div>
          </li>

        </ul>

      </div>
      <div class="slide_right">
        <div class="block">
           <el-carousel trigger="click" height="400px" >
             <el-carousel-item v-for="(item,ins) in arr" :key="ins">
               <img :src="item.imgSrc" alt="">
             </el-carousel-item>
           </el-carousel>
         </div>
      </div>

    </div>
  </div>
</template>

<script>
  export default{
    data(){
      return{
        index:0,
        goods:{},
        arr:[]
      }
    },
    mounted(){
		this.axios.get(this.ojbk+"/front/goods_classify").then((res)=>{
			console.log(res.data)
      this.goods=res.data
		});
    this.axios.get(this.ojbk+'/front/banner').then((res)=>{
      console.log(res.data)
       this.arr=res.data
    })
	},
    methods:{
      move(index){

      }
    }
  }
</script>

<style>
  .shangou{
    width: 100%;
    /* background-color: #F5F5F5; */
  }
  .shangou_box{
    width: 1300px;
    height: 400px;
    margin: 0 auto;
    display: flex;
    background-color: #F5F5F5;
  }
  .slide_left{
    flex: 1.5;
    height: 100%;
    background-color: #424242;
    padding: 20px 0;
    box-sizing: border-box;
    position: relative;
  }
  .slide_left li:hover .goods_box_list{
    display: block;
  }
  .goods_box_list{
    position: absolute;
    overflow: hidden;
    max-width: 1100px;
    min-width: 800px;
    display: none;
    z-index: 10;
    height: 400px;
    left: 200px;
    top: 0;
    background-color: #FFFFFF;
    color: #424242;
  }
  .goods_box_list ul{
    display: flex;
    flex-wrap: wrap;
    margin: 0;
  }
  .goods_box_list ul li{
      color: #424242;
      width: 200px;
      height:60px;
      font-size: 10px;
      text-align: center;
      overflow: hidden;

      transition: 0.5s;
  }
  .arrlist{

  }
  .goods_box_list ul li:hover{
    background-color: #FFFFFF;
    color: red;
    cursor: pointer;
  }
  .goods_box_list div{
    display: flex;
  }
  .goods_box_list div:hover{
    cursor: pointer !important;
  }
  .goods_box_list div img{
    margin-top: 20px;
    width: 40px;
    height: 40px;
  }
  .goods_box_list div p{
      padding-top:20px;
      box-sizing: border-box;
      padding-left: 20px;
      width: 200px;
  }
  .slide_left ul{
    width: 100%;
  }
  .slide_left li{
    list-style: none;
    height: 36px;
    line-height: 36px;
    color: #FFFFFF;
    width: 100%;
    padding:0 20px;
  }
  .slide_left_list li:hover{
    background-color: orange;
    color: #FFFFFF;
    cursor:pointer;
  }
  .slide_right{
    flex: 8;
  }
  .block img{
    width: 100%;
    height: 100%;
  }
</style>



