<template>
	<div class="login">
    <div class="login_box">
        <div></div>
    </div>
    <div class="login_main">
       <div class="login_panel">
          <div class="login_panel_box">
              <div class="login_title">
                <h3>账号登录</h3>
              </div>
              <div class="login_input_box">
                <el-input v-model="phone" placeholder="手机号"></el-input>
              </div>
              <div class="login_input_box">
                <el-input v-model="pas" placeholder="密码" type="password"></el-input>
              </div>
             <div class="code">
                 <el-input v-model="code" placeholder="验证码" type="text"></el-input>
                   <span @click="getcodeimgurl"><img :src="codeimgurl" alt=""></span>
              </div>
              <div class="login_input_btn">
                <el-button type="warning" plain @click="login">登录</el-button>
              </div>
              <div class="login_register">
                <router-link to="/register">立即注册</router-link>
                <span>|</span>
                <a href="">忘记密码</a>
              </div>
          </div>
       </div>
    </div>
    <div class="bottomos">
        <div class="bottom_box">
          <ul>
            <li>简体</li>
            <li>繁体</li>
            <li>EngLish</li>
            <li>常见问题</li>
            <li>隐私政策</li>
          </ul>
        </div>
        <div class="bottom_box">
          <p>小米公司版权所有-京ICP备10046444-  <span><img src="https://account.xiaomi.com/static/res/9204d06/account-static/respassport/acc-2014/img/ghs.png" alt=""></span>京公网安备11010802020134号-京ICP证110507号</p>
        </div>
    </div>

  </div>
</template>

<script>

  export default{
    data(){
      return{
        phone:'',
        pas:'',
        codeimgurl:'',
        code:'',
        "captchaImgUrl":String
      }
    },
    mounted(){
      this.axios.get(this.ojbk+'/auth/create_kaptcha',).then((res)=>{
        console.log("hello")
        console.log(res)
         this.codeimgurl=res.data.kaptchaImgUrl
         // console.log(res.data)
       })
    },
    methods:{
      getcodeimgurl(){
        this.axios.get(this.ojbk+'/auth/create_kaptcha',).then((res)=>{
          console.log("hello")
          console.log(res)
           this.codeimgurl=res.data.kaptchaImgUrl
           // console.log(res.data)
         })
      },

      login(){
        this.axios.put(this.ojbk+'/user/signin',{
          "userName":this.phone,
          "passWord":this.pas,
          "captchaImgValue":this.code,
          "captchaImgUrl":this.codeimgurl
        }).then((res)=>{

          if(res.data.code=="00000"){
            this.$message("登录成功")
            // this.$router.push("/home")
            this.$store.commit("preservationName",this.phone)
            this.$router.replace({path:'/home'})
            console.log(res)
            // this.$store.commit("preservationName",res.data.userMessage.userName)
            
          }

        })
      }
    }
  }
</script>

<style scoped="scoped">
  .login_box{
    width: 100%;
  }
  .login_box div{
    margin:  0 auto;
    width: 1300px;
    height: 100px;
    background: url("https://xiaomi-shopmall.oss-cn-guangzhou.aliyuncs.com/front/mistore_logo.png") no-repeat;
  }
  .login_main{
    height: 600px;
    width: 100%;
    background: url("https://xiaomi-shopmall.oss-cn-guangzhou.aliyuncs.com/front/login_bg.jpg") no-repeat center;
  }
  .login_panel{
    width: 1300px;
    margin: 0 auto;
    position: relative;
  }
  .login_panel_box{
    width: 400px;
    height: 550px;
    background-color: #FFFFFF;
    position: absolute;
    left: 850px;
    top: 30px;
  }
  .bottomos{
    width: 100%;
  }
  .bottom_box{
    width: 1300px;
    margin: 0 auto;

    display: flex;
  }
  .bottom_box ul{
    display: flex; margin: 0 auto;
  }
  .bottom_box ul li{
    padding: 20px;
    list-style: none;
    font-size: 12px;
    color: #D8D8D8;
  }
  .bottom_box p{
    margin: 0 auto;
    font-size: 12px;
  }

  /* 登录面板 */
  .login_title{
    height: 50px;
    line-height: 50px;
    text-align: center;
    color: #FFA500;
  }
  .login_input_box{
    padding: 10px 40px;
  }
  .login_input_btn{
    text-align: center;
    padding: 10px 40px;
  }
  .login_input_btn button{
    width: 100%;
  }
  .login_register{
    text-align: center;
    margin-top: 20px;
  }
  .login_register a{
    text-decoration: none;
    font-size: 12px;
  }
  /* 验证码样式 */
  .code{
   /* width: 100%; */
    padding: 10px 40px;
    display: flex;
  }
  .code img{
    height: 40px;
  }
  /* 底部 */
  .bottom_box{
    height: 40px;
    display: flex;

    margin-bottom: 20px;
    color: #A1A1A1;
  }

</style>



