<template>
  <div class="homenav">
    <div class="homenav_box">
    <div class="logo"><img src="~@/assets/icon/homelogin.png" alt=""></div>
   <div class="navshow">
      <ul>
        <div>
          <li v-for="(item,index) in nav_goods" :key="index" @mouseover="over(item,index)">{{item.name}}

          </li>
           <div class="disp">
             <div class="disp_middle">
              <div v-for="(iu,ints) in nav_id.goodsDirBean"  class="disp_box">
                <router-link :to="'/details/'+iu.goodsId">
                  <div class="listimg"><img :src="iu.originalImg" alt=""></div>
                  <div class="disp_text"><p>{{iu.goodsName}}</p></div>
                  </router-link>
              </div>
            </div>
           </div>

        </div>


      </ul>
    </div>
    <div>
      <div class="demo-input-suffix">
      <el-input
          placeholder="请选择日期"
          suffix-icon="el-icon-date"
          v-model="value">
        </el-input>
        </div>
    </div>
    </div>
  </div>
</template>

<script>
  export default{
    data(){
      return{
        value:'',
        nav_goods:{},
        nav_id:{}
      }
    },
    mounted() {
      this.axios.get(this.ojbk+"/admin/get_goods_dir").then((res)=>{
        this.nav_goods=res.data
        console.log(this.nav_goods)
        console.log(res)
      })
	  // this.axios.get("http://120.24.23.184:8080/getmessage").then((res)=>{
	  //  console.log("跨域的数据")
	  //  console.log(res)
	  // })
    },
    methods:{
     over(item,index){
       // console.log(index)
       console.log(item)
       this.nav_id=item
       console.log(this.nav_id.goodsDirBean+"helloe")

     }
    }
  }

</script>

<style scoped="scoped">
  .homenav{
    height: 100px;
    background-color: #F5F5F5;
  }
  .homenav_box{
    margin: 0 auto;
    width: 1300px;
    overflow: hidden;
    height: 100px;
    display: flex;
    align-items: center;
    background-color: #F5F5F5;
  }

  .logo{
    flex:2;
  }
  .navshow{
    flex: 8;

  }
  .navshow ul {
    width: 500px;
    margin: 0 auto;

  }
  .navshow ul div{
    display: flex;
    text-align: center;
/*    width: 500px;
    margin: 0 auto; */
  }


  .navshow ul div li{
    list-style: none;
    padding: 0 10px;
    height: 100px;
    line-height: 100px;
    transition: 0.6s;
  }
  .navshow ul div li:hover{
    color: red;
    text-shadow: #5F5750 0.6px 1px;
    cursor: pointer;
  }

  .disp{
    /* display: block; */
    width: 100%;
    height: 0px;
    background-color: #FFFFFF;
    z-index: 20;
    position: absolute;
    top: 140px;
    left: 0px;
    margin: 0 auto;
    transition: 0.6s;
    /* width: 1300px; */
    min-width: 1300px;
    overflow: hidden;
    /* height: 200px; */
  }
  .navshow ul div:hover .disp{
    /* display: block; */
    height: 200px;
  }
  .listimg{
    min-width: 100%;
    height: 100px;
    text-align: center;
    /* background-color: #FF0000; */

  }
  .listimg img{
    width: 130px;
    height: 100px;
    margin: 0 auto;
  }
  .disp_box{
    display: block!important;
    min-width: 260px;
    max-width: 260px;
    height: 200px;
    font-size: 12px;
    color: #ababab;
    padding-top: 20px;

    transition: 0.6s;
   }
   .disp_box:hover{
       color: darkred;
       cursor: pointer;
       text-shadow: #5F5750 0.6px 1px;
    }
  .disp_text{
      /* background-color: #42B983; */
      float: left;
      height:100px;
      width: 100%;
      padding: 0 20px;
      text-indent: 20px;
      text-align: center !important;
      box-sizing: border-box;
      margin-top: 20px;
  }
  .disp_text p{
    text-align: center;
    width: 200px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  .disp_middle{
    width: 1300px;
    margin: 0 auto;
    overflow: hidden;
    display: block;

  }


</style>



