<template>
  <div class="phone_big_box">
  <div class="phone">
    <h3>手机</h3>
    <div class="phone_box">
      <div class="phone_left"><img src="https://cdn.cnbj1.fds.api.mi-img.com/mi-mall/574c6643ab91c6618bfb2d0e2c761d0b.jpg?thumb=1&w=293&h=768&f=webp&q=90" alt=""></div>
      <div class="phone_right">
        <ul>
          <li v-for="p in phone">
            <div><img :src="p.originalImg" alt=""></div>
            <div><p>{{p.goodsName}}</p></div>
            <div><p>{{p.desc}}</p></div>
            <div class="cred"><b>￥{{p.shopPrice}}元</b></div>
          </li>
        </ul>
      </div>
    </div>
  </div></div>
</template>

<script>
  // import res from '@/assets/data/swi.js'
  export default{
    data(){
      return {
        phone:[]
      }
    },
    mounted() {
      // this.phone=res.data.list
      this.axios.get(this.ojbk+"/front/goods_classify").then((res)=>{
      console.log(res)
      this.phone=res.data[0].indexGoodsBeans
      console.log(this.phone)
      console.log("nihao ")
      // this.phone=res.data.手机.indexGoodsBeans;
      // console.log("hello")
      // console.log(this.phone)
     })
    }
  }
</script>

<style>
  .cred{
    color: orangered;
    margin-top: 20px;
    font-size: 16px;
  }

  .phone_big_box{
    width: 100%;
    background-color: #f5f5f5;
    margin-bottom: 25px;
  }
  .phone{
    width: 1300px;
    margin: 0 auto;
	 background-color: #F5F5F5;
  }
  .phone h3{
    padding: 20px 0;
  }
  .phone_box{
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
  }
  .phone_left img{
    width: 230px;
    height: 600px;
  }
  .phone_right{
    height: 600px;
    width: 1070px;
  }
  .phone_right ul{
    display: flex;
    flex-wrap:wrap;
    height: 600px;
    overflow: hidden;
  }
  .phone_right ul li{

    list-style: none;
    text-align: center;
    background-color: #FFFFFF;
    width: 250px;
    height: 292px;
    margin-left: 17px;
    margin-bottom: 20px;
    color: #A1A1A1;
    font-size: 12px;
    padding-top: 30px !important;
    /* padding: 20px 0; */
    padding: 0 20px;
    box-sizing: border-box;
    transition: 0.3s;
  }
  .phone_right ul li:hover{
     box-shadow:  2px 2px 10px #e1e1e1;
  }
  .phone_right ul li img{
    width: 160px;
    height: 110px;
  }
</style>



