import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
const store = new Vuex.Store({
  strict:true,
  state:{
     name:''
  },
  mutations:{
     preservationName(state,name){
         this.state.name=name
     }

  },
  actions:{},
  getters:{},
  modules:{}
})
export default store



